.map {
	/* height: 500px; */
	height: 400px;
	background-color: white;
	padding: 1rem;
	border-radius: 20px;
	margin-top: 16px;
	box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);
	/* margin-bottom: 10px; */
}

.map .leaflet-container {
	height: 100%;
}

/* PopUp */
.popup-container {
	width: 150px;
}

.popup-flag {
	height: 80px;
	width: 100%;
	background-size: cover;
	border-radius: 8px;
}

.popup-name {
	font-size: 20px;
	font-weight: bold;
	color: #555;
}

.popup-cases .popup-recovered .popup-deaths {
	font-size: 16px;
	margin-top: 5px;
}

@media (max-width: 990px) {
	.map {
		margin-bottom: 10px;
	}
}
